.react-datepicker__day{
    width: 35px;
    height: 35px;
    line-height: 35px;
    

    // Styles provided
    &--weekend{
        background-color:  adjust-hue($color: #ebe7e7, $degrees: 0);
    }

    &--selected{
        background-color: darkblue
    }
    
    // Style from custom datepicker component
    &.with-users {
        color: red;
    }
}


